const app = getApp()
import helper from '../common/helper.js';
export default {
	data() {
		return {
			indicatorDots: true,
			autoplay: true,
			interval: 2000,
			duration: 500,
			Inv: 0,
			boardId: '',
			pageNode: [],
			imgUrl: '',
			page_size: 5,
			page:1,
			listNode: []
		};
	},
	onLaunch() {
		
	},
	onShow() {
		
	},
	onHide() {
		
	},
	onLoad() {
		this.imgUrl = helper.imgUrl
		// this.getUserInfo()
		// this.getListMore()
		this.getList()
	},
	methods: {
		// 是否获取过token
		getIsToken(){
			if (app.globalData.token == "") {
				// 获取缓存中用于登录的用户名和密码
				// 如果没有缓存信息,不进行登录,用户点击操作时,提示进入登录页
				const loginName = uni.getStorageSync('login_name');
				const loginPwd = uni.getStorageSync('login_pwd');
				// console.log(loginName + '---===---' + loginPwd)
				if (loginName == '' || loginPwd == '') {
					uni.showToast({
						title: '未检测到用户的登录记录，请进行登录',
						icon: 'none',
						duration: 3000
					});
					setTimeout(() => {
						// 进入登录页
						uni.reLaunch({
							url: './login'
						});
					}, 3000)
					
				} else {
					// 执行登录操作
					this.runLogin(loginName, loginPwd)
				}
			} else {
				// 获取用户信息
				this.getUserInfo()
				// this.getList()
			}
		},
		// 进行登录操作
		runLogin(loginName, loginPwd) {
			// uni.showLoading({
			//   title: '登录中...',
			// 	duration: 1000000
			// });
			uni.request({
				url: `${helper.requestUrl}/login`,
				method: 'POST',
				data: {
					username: loginName,
					password: loginPwd
				},
				success: res => {
					console.log(res);
					uni.hideLoading();
					res = helper.null2str(res)
					if (res.statusCode == 200) {
						// 登录的账号和密码存入缓存
						uni.setStorageSync('login_name', this.loginName);
						uni.setStorageSync('login_pwd', this.loginPaw);
						// uni.showToast({
						// 	title: '登录成功',
						// 	icon: "none"
						// });
						app.globalData.token = `${res.data.token_type} ${res.data.access_token}`
					} else {
						uni.showToast({
							title: res.data.message,
							icon: 'none',
							duration: 3000
						});
						setTimeout(() => {
							// 进入登录页
							uni.reLaunch({
								url: './login'
							});
						}, 3000)
					}
				}
			});
		},
		// 导航详情
		goNavs(e) {
			let link = e.currentTarget.dataset.link
			let bind_board = e.currentTarget.dataset.bind_board
			let id = e.currentTarget.dataset.id
			let name = e.currentTarget.dataset.name
			if (app.globalData.token == "") {
				const loginName = uni.getStorageSync('login_name');
				const loginPwd = uni.getStorageSync('login_pwd');
				// console.log(loginName + '---===---' + loginPwd)
				if (loginName == '' || loginPwd == '') {
					uni.showToast({
						title: '未检测到用户的登录记录，请进行登录',
						icon: 'none',
						duration: 3000
					});
					setTimeout(() => {
						// 进入登录页
						uni.reLaunch({
							url: './login'
						});
					}, 3000)
					
				} else {
					// 执行登录操作
					this.runLogin(loginName, loginPwd)
					if (bind_board == '0') {
						uni.navigateTo({
							url: `/pages/${link}`
						})
					} else{
						uni.navigateTo({
							url: `/pages/indexA?id=${bind_board}&name=${name}`
						})
					}
				}
			}else{
				if (bind_board == '0') {
					uni.navigateTo({
						url: `/pages/${link}`
					})
				} else{
					uni.navigateTo({
						url: `/pages/indexA?id=${bind_board}&name=${name}`
					})
				}
			}
			// console.log(e.currentTarget.dataset.id)
			
			// console.log(bind_board)
		},
		//
		selListType(e) {
			this.Inv = e.currentTarget.dataset.index
			this.boardId = e.currentTarget.dataset.block_id
			// console.log(this.boardId,'222')
			this.page = '1'
			this.listNode = []
			this.getListMore()
		},
		// 轮播跳转
		goBanner(e) {
			// console.log(e)
		},
		// 文章详情
		goDetail(e) {
			if (app.globalData.token == "") {
				const loginName = uni.getStorageSync('login_name');
				const loginPwd = uni.getStorageSync('login_pwd');
				// console.log(loginName + '---===---' + loginPwd)
				if (loginName == '' || loginPwd == '') {
					uni.showToast({
						title: '未检测到用户的登录记录，请进行登录',
						icon: 'none',
						duration: 3000
					});
					setTimeout(() => {
						// 进入登录页
						uni.reLaunch({
							url: './login'
						});
					}, 3000)
					
				} else {
					// 执行登录操作
					this.runLogin(loginName, loginPwd)
				}
			}else{
				uni.navigateTo({
					url: `/pages/articleDetail?id=${e.currentTarget.dataset.id}`
				})
			}
			
		},
		getUserInfo() {
			// 用户信息获取
			uni.showLoading({
			  title: '用户信息获取中...'
			});
			uni.request({
				url: `${helper.requestUrl}/me`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res)
					console.log(res,'++++++++')
					this.userInfo = res.data
					app.globalData.userInfo = res.data
					console.log(this.userInfo.mobile)
				}
			})
		},
		//获取数据
		getList() {
			uni.showLoading({
			  title: '加载中...',
				duration: 1000000
			});
			uni.request({
				url: `${helper.requestUrl}/index`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res)
					if (res.data.status_code == 200) {
						let pageNode = res.data.data
						this.pageNode = pageNode
						if (pageNode.board_data.length > 0){
							// this.boardId = pageNode.board_data[0].block_id
							this.boardId = pageNode.board_data[0].id
							console.log(this.boardId,'999')
							this.getListMore()
						}
					} else {
						uni.showToast({
							title: res.data.message
						});
					}

				}
			})
		},
		//加载更多
		onReachBottom() {
			// console.log(this.boardId)
			this.page ++;
			// console.log(this.page)
			// console.log(this.pageNode.board_data[Inv].block_id)
			uni.showLoading({
			  title: '加载中...',
				duration: 1000000
			});
			this.getListMore()
		},
		getListMore(){
			uni.request({
				url: `${helper.requestUrl}/index-board-posts`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				data: {
					board_id: this.boardId,
					page_size:this.page_size,
					page:this.page
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res)
					if (res.data.status_code == 200) {
						console.log('888',res.data.data)
						console.log(this.pageNode.board_data[this.Inv].posts,'*****')
						if (res.data.data.length > 0) {
							this.listNode = this.listNode.concat(res.data.data)
							// this.pageNode.board_data[this.Inv].posts = this.pageNode.board_data[this.Inv].posts.concat(res.data.data)
							
							// console.log('//',this.pageNode)
							// console.log(res.data.data)
							// this.pageNode = this.pageNode.concat(res.data.data)
							// console.log(this.pageNode)
						} else {
							uni.showToast({
								title:"没有更多数据了",
								icon:"none"
							})
						}
					} else {
						uni.showToast({
							title: res.data.message
						});
					}
			
				}
			})
		}
	}
}
